.header {
  width: map_get($width, full);
  height: 100vh;
  background-image: $bgHeader;
  background-position: center;
  background-size: cover;

  &__top {
    @extend %flex-row;
    justify-content: space-around;
    color: map_get($colors, bg-white);
    font-weight: 400;
    text-transform: uppercase;
  }

  &__logo {
    position: relative;
    left: rem(-6);
    width: map_get($width, logo);
    margin-top: rem(25);
  }

  &__phone,
  &__call {
    @extend %font-header;
    position: relative;
    margin-top: rem(60);
    color: map_get($colors, bg-white);
    text-decoration: none;
    transform: translateX(34px);
    transition: $transition-hover;
  }

  &__phone {
    order: -1;
    transform: translateX(-31px);
  }

  &__call::before {
    @include before(97%,1px);
    background-color: map_get($colors, bg-white);
    left: 0;
    top: rem(20);
  }

  &__phone:hover,
  &__call:hover {
    opacity: 0.5;
  }

  &__title {
    position: relative;
    margin: 44vh rem(125) 0;
    color: map_get($colors, bg-white);
    font-size: rem(59);
    font-weight: bold;
    line-height: 1;
    text-transform: capitalize;
  }

  &__title::before {
    @include before(35px,2px);
    background-color: map_get($colors, bg-white);
    left: 0;
    top: rem(-20);
  }

  &__nav {
    margin: rem(92) rem(100) rem(52);
  }
}
